import React from "react";
import {
  Container,
  Title,
  TextField,
  Textarea,
  Button,
  Maintitle,
  Group,
  MaintitleLets,
} from "./contact";

export default function Contact({ children, ...restProps }) {
  return <Container {...restProps}>{children}</Container>;
}

Contact.Maintitle = function ContactMaintitle({ children, restProps }) {
  return <Maintitle {...restProps}>{children}</Maintitle>;
};

Contact.MaintitleLets = function ContactMaintitleLets({ children, restProps }) {
  return <MaintitleLets {...restProps}>{children}</MaintitleLets>;
};

Contact.Title = function ContactTitle({ children, restProps }) {
  return <Title {...restProps}>{children}</Title>;
};

Contact.Group = function ContactGroup({ children, restProps }) {
  return <Group {...restProps}>{children}</Group>;
};

Contact.TextField = function ContactTextField({ children, ...restProps }) {
  return <TextField {...restProps}>{children}</TextField>;
};

Contact.Textarea = function ContactTextarea({ children, ...restProps }) {
  return <Textarea {...restProps}>{children}</Textarea>;
};

Contact.Button = function ContactButton({ children, ...restProps }) {
  return <Button {...restProps}>{children}</Button>;
};
