import styled from "styled-components/macro";
import { contentColor, backgroundColorY } from "../../themes/color";
import { Link as ReactRouterLink } from "react-router-dom";
import { FaBars } from "react-icons/fa";

export const Container = styled.div`
  height: 65px;
  width: 100%;
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
  ${"" /* box-shadow: 0 10px 5px 2px rgba(0,0,0,.2); */}
`;

export const HoverGroup = styled.div`
  display: none;
`;

export const Group = styled.div`
  align-items: center;
  margin-left: 60px;
  margin-right: 60px;
  color: ${contentColor};

  @media (max-width: 1050px) {
    display: none;
  }
`;

export const Mobile = styled(FaBars)`
  display: none;
  align-items: center;
  margin-left: 60px;
  margin-right: 60px;
  color: ${contentColor};

  @media (max-width: 1050px) {
    display: block;
  }

  &:hover {
    cursor: pointer;
  }
`;

export const Group1 = styled.div`
  align-items: center;
  margin-left: 60px;
  margin-right: 60px;
  color: ${contentColor};
`;

export const List = styled.ul`
  list-style-type: none;
`;

export const ListLi2 = styled.li`
  margin-left: 25px;
  margin-right: 25px;
`;

export const ListLi = styled.li`
  margin-left: 25px;
  margin-right: 25px;
  display: inline;
  }
`;

export const ListA = styled(ReactRouterLink)`
  text-decoration: none;
  font-size: 24px;
  color: inherit;
  color: ${({ active }) => (active ? backgroundColorY : "inherit")};

  &:hover {
    color: ${backgroundColorY};
    transition: 0.5s all ease;
  }
`;

export const Name = styled.p`
  font-size: 24px;
  display: inline;

  @media (max-width: 650px) {
    font-size: 1rem;
  }
`;

export const Initial = styled.p`
  font-size: ${({ size }) => (size ? size : "34px")};
  display: inline;
  color: ${backgroundColorY};

  @media (max-width: 650px) {
    font-size: 2rem;
  }
`;
