import React from "react";
import { Container, Group, Title, Image , Link } from "./mywork";

export default function Mywork({ children, ...restProps }) {
  return <Container {...restProps}>{children}</Container>;
}

Mywork.Group = function MyworkGroup({ children, ...restProps }) {
  return <Group {...restProps}>{children}</Group>;
};

Mywork.Title = function MyworkTitle({ children, ...restProps }) {
  return <Title {...restProps}>{children}</Title>;
};

Mywork.Image = function MyworkImage({ children, ...restProps }) {
  return <Image {...restProps}>{children}</Image>;
};

Mywork.Link = function MyworkLink({ children, ...restProps }) {
  return <Link {...restProps}>{children}</Link>;
};
