import React from "react";
import { Header, Navbar } from "../../components";
import ExtendedWorksContent from "../../components/extendedworkcontent";
import { about } from "../../data/about";

export default function ExtendedWorkContentContainer({
  children,
  ...restProps
}) {
  const { onlyImage } = restProps.location.state;
  const { video } = restProps.location.state;
  const { title } = restProps.location.state;
  const { images } = restProps.location.state;
  const { videoName } = restProps.location.state;
  const { qrImageName } = restProps.location.state;
  const { imageFolderName } = restProps.location.state;
  const { cornerImageName } = restProps.location.state;
  return (
    <Header showImage={false} height>
      <Navbar>
        <Navbar.Group>
          <Navbar.List>
            <Navbar.ListLi>
              <Navbar.ListA to={process.env.PUBLIC_URL}>Home</Navbar.ListA>
            </Navbar.ListLi>
            <Navbar.ListLi>
              <Navbar.ListA to={process.env.PUBLIC_URL +"/aboutme"}>About Me</Navbar.ListA>
            </Navbar.ListLi>
            <Navbar.ListLi>
              <Navbar.ListA active to={process.env.PUBLIC_URL +"/myworks"}>
                My Works
              </Navbar.ListA>
            </Navbar.ListLi>
            <Navbar.ListLi>
              <Navbar.ListA to={process.env.PUBLIC_URL +"/contact"}>Contact</Navbar.ListA>
            </Navbar.ListLi>
          </Navbar.List>
        </Navbar.Group>
        <Navbar.Group>
          <Navbar.Name>
            <Navbar.Initial>{about.initial}</Navbar.Initial> {about.name}
          </Navbar.Name>
        </Navbar.Group>
      </Navbar>
      {!onlyImage ? (
        <ExtendedWorksContent>
          <ExtendedWorksContent.Title>{title}</ExtendedWorksContent.Title>
          {images.map((item) => {
            return (
              <ExtendedWorksContent.Image
                src={process.env.PUBLIC_URL + "/images/works/" + imageFolderName + "/" + item}
                alt={"Images unavailable"}
              />
            );
          })}
          {video ? (
            <ExtendedWorksContent.VideoGroup>
              <ExtendedWorksContent.Video autoPlay muted loop>
                <source
                  src={process.env.PUBLIC_URL + "/images/works/" + imageFolderName + "/" + videoName}
                  type="video/mp4"
                />
              </ExtendedWorksContent.Video>
            </ExtendedWorksContent.VideoGroup>
          ) : null}
        </ExtendedWorksContent>
      ) : (
        <>
          <ExtendedWorksContent>
            {images.map((item) => {
              return (
                <ExtendedWorksContent.Image
                  src={process.env.PUBLIC_URL + "/images/works/" + imageFolderName + "/" + item}
                  alt={"Images unavailable"}
                />
              );
            })}
          </ExtendedWorksContent>
          {video ? (
            <ExtendedWorksContent>
              <ExtendedWorksContent.VideoGroup>
                <ExtendedWorksContent.Video autoPlay muted loop>
                  <source
                    src={process.env.PUBLIC_URL + "/images/works/" + imageFolderName + "/" + videoName}
                    type="video/mp4"
                  />
                </ExtendedWorksContent.Video>

                <ExtendedWorksContent.QrImage
                  src={process.env.PUBLIC_URL + "/images/works/" + imageFolderName + "/" + qrImageName}
                  alt={"Images unavailable"}
                />
                <ExtendedWorksContent.OverlayText>
                  Scan QR code for full walkthrough video
                </ExtendedWorksContent.OverlayText>
              </ExtendedWorksContent.VideoGroup>
              {/* <ExtendedWorksContent.Image
                src={
                  "./images/works/" + imageFolderName + "/" + cornerImageName
                }
                alt={"Images unavailable"}
              /> */}
            </ExtendedWorksContent>
          ) : null}
        </>
      )}
    </Header>
  );
}
