import "./App.css";
import {
  HeaderContainer,
  AboutmeContainer,
  MyworkContainer,
  ContactContainer,
  ExtendedContainer,
  ExtendedWorkContentContainer,
  ExtendedWorkIndividualContentContainer,
  extendedworkcollegecontentContainer,
  extendedworkofficecontentContainer,
  extendedworkrendercontentContainer,
} from "./containers";
import { BrowserRouter as Router, Route } from "react-router-dom";

function App() {
  return (
    <Router exact basename="/">
      <Route
        exact
        path={process.env.PUBLIC_URL + "/"}
        component={HeaderContainer}
      />
      <Route
        exact
        path={process.env.PUBLIC_URL + "/aboutme"}
        component={AboutmeContainer}
      />
      <Route
        exact
        path={process.env.PUBLIC_URL + "/myworks"}
        component={MyworkContainer}
      />
      <Route
        exact
        path={process.env.PUBLIC_URL + "/contact"}
        component={ContactContainer}
      />
      <Route
        exact
        path={process.env.PUBLIC_URL + "/extendedwork"}
        component={ExtendedContainer}
      />
      <Route
        exact
        path={process.env.PUBLIC_URL + "/extendedworkcontent"}
        component={ExtendedWorkContentContainer}
      />
      <Route
        exact
        path={process.env.PUBLIC_URL + "/extendedworkindividualcontent"}
        component={ExtendedWorkIndividualContentContainer}
      />
      <Route
        exact
        path={process.env.PUBLIC_URL + "/extendedworkcollegecontent"}
        component={extendedworkcollegecontentContainer}
      />
      <Route
        exact
        path={process.env.PUBLIC_URL + "/extendedworkofficecontent"}
        component={extendedworkofficecontentContainer}
      />
      <Route
        exact
        path={process.env.PUBLIC_URL + "/extendedworkrendercontent"}
        component={extendedworkrendercontentContainer}
      />
    </Router>
  );
}

export default App;
