import React, { useState } from "react";
import { Header, Mywork, Navbar, Slidebar } from "../../components";
import { about } from "../../data/about";
import {
  mainImageOffice,
  mainImageAcademic,
  mainImageIndividual,
  mainImageRender,
} from "../../data/works";

export default function MyworkContainer({ children, ...restProps }) {
  const [showSlidebar, changeSlidebar] = useState(false);

  var sliderFunction = () => {
    changeSlidebar(!showSlidebar);
  };
  return (
    <Header showImage={false}>
      {showSlidebar ? (
        <Slidebar>
        <Slidebar.CloseButton onClick={sliderFunction} />
          <Slidebar.Group>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL}
              onClick={sliderFunction}
            >
              Home
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/aboutme"}
              onClick={sliderFunction}
            >
              About Me
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/myworks"}
              onClick={sliderFunction}
            >
              My Works
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/contact"}
              onClick={sliderFunction}
            >
              Contact
            </Slidebar.LinkText>
          </Slidebar.Group>
        </Slidebar>
      ) : null}
      {showSlidebar ? null : (
        <>
          <Navbar>
            <Navbar.Group>
              <Navbar.List>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL}>Home</Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL + "/aboutme"}>
                    About Me
                  </Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA active to={process.env.PUBLIC_URL + "/myworks"}>
                    My Works
                  </Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL + "/contact"}>
                    Contact
                  </Navbar.ListA>
                </Navbar.ListLi>
              </Navbar.List>
            </Navbar.Group>
            <Navbar.Mobile onClick={sliderFunction} />
            <Navbar.Group1>
              <Navbar.Name>
                <Navbar.Initial>{about.initial}</Navbar.Initial> {about.name}
              </Navbar.Name>
            </Navbar.Group1>
          </Navbar>
          <Mywork>
            <Mywork.Group>
              <Mywork.Link
                to={process.env.PUBLIC_URL + "/extendedworkcollegecontent"}
              >
                <Mywork.Title>
                  Academic
                  <br />
                  works
                </Mywork.Title>
                <Mywork.Image
                  src={
                    process.env.PUBLIC_URL +
                    "/images/" +
                    mainImageAcademic.mainFolder +
                    "/" +
                    mainImageAcademic.subFolder +
                    "/" +
                    mainImageAcademic.imageName
                  }
                />
              </Mywork.Link>
            </Mywork.Group>
            <Mywork.Group>
              <Mywork.Link
                to={process.env.PUBLIC_URL + "/extendedworkofficecontent"}
              >
                <Mywork.Title>
                  Professional
                  <br />
                  works
                </Mywork.Title>
                <Mywork.Image
                  src={
                    process.env.PUBLIC_URL +
                    "/images/" +
                    mainImageOffice.mainFolder +
                    "/" +
                    mainImageOffice.subFolder +
                    "/" +
                    mainImageOffice.imageName
                  }
                />
              </Mywork.Link>
            </Mywork.Group>
            <Mywork.Group>
              <Mywork.Link
                to={process.env.PUBLIC_URL + "/extendedworkindividualcontent"}
              >
                <Mywork.Title>
                  Individual
                  <br />
                  works
                </Mywork.Title>
                <Mywork.Image
                  src={
                    process.env.PUBLIC_URL +
                    "/images/" +
                    mainImageIndividual.mainFolder +
                    "/" +
                    mainImageIndividual.subFolder +
                    "/" +
                    mainImageIndividual.imageName
                  }
                />
              </Mywork.Link>
            </Mywork.Group>
            <Mywork.Group>
              <Mywork.Link
                to={process.env.PUBLIC_URL + "/extendedworkrendercontent"}
              >
                <Mywork.Title>
                  Render
                  <br />
                  works
                </Mywork.Title>
                <Mywork.Image
                  src={
                    process.env.PUBLIC_URL +
                    "/images/" +
                    mainImageRender.mainFolder +
                    "/" +
                    mainImageRender.subFolder +
                    "/" +
                    mainImageRender.imageName
                  }
                />
              </Mywork.Link>
            </Mywork.Group>
          </Mywork>
        </>
      )}
    </Header>
  );
}
