import React, { createContext, useContext, useEffect, useState } from "react";
import {
  Container,
  Name,
  Group,
  OwnerName,
  NameBold,
  Group1,
  Button,
} from "./header";

export const HideProvider = createContext();

export default function Header({ children, ...restProps }) {
  const [hide, setHide] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setHide(true);
    }, 2500);
  }, []);
  return (
    <HideProvider.Provider value={{ hide, setHide }}>
      <Container {...restProps}>{children}</Container>
    </HideProvider.Provider>
  );
}

Header.Group = function HeaderGroup({ children, ...restProps }) {
  const { hide } = useContext(HideProvider);
  console.log(hide);
  return (
    <Group {...restProps} hide={hide}>
      {children}
    </Group>
  );
};

Header.Group1 = function HeaderGroup1({ children, ...restProps }) {
  const { hide } = useContext(HideProvider);
  console.log(hide);
  return (
    <Group1 {...restProps} hide={hide}>
      {children}
    </Group1>
  );
};

Header.Name = function HeaderName({ children, ...restProps }) {
  return <Name {...restProps}>{children}</Name>;
};

Header.NameBold = function HeaderNameBold({ children, ...restProps }) {
  return <NameBold {...restProps}>{children}</NameBold>;
};

Header.OwnerName = function HeaderOwnerName({ children, ...restProps }) {
  return <OwnerName {...restProps}>{children}</OwnerName>;
};

Header.Button = function HeaderButton({ children, ...restProps }) {
  return <Button {...restProps}>{children}</Button>;
};
