import React from "react";
import {
  Container,
  Title,
  Image,
  Content,
  Group,
  Link,
  RenderGroup,
  RenderContainer,
  RenderImage,
} from "./extendedWorks";

export default function ExtendedWorks({ children, ...restProps }) {
  return <Container {...restProps}>{children}</Container>;
}

ExtendedWorks.Group = function ExtendedWorksGroup({ children, ...restProps }) {
  return <Group {...restProps}>{children}</Group>;
};

ExtendedWorks.RenderContainer = function RenderContainerGroup({ children, ...restProps }) {
  return <RenderContainer {...restProps}>{children}</RenderContainer>;
};

ExtendedWorks.RenderGroup = function ExtendedWorksRenderGroup({
  children,
  ...restProps
}) {
  return <RenderGroup {...restProps}>{children}</RenderGroup>;
};

ExtendedWorks.Title = function ExtendedWorksTitle({ children, ...restProps }) {
  return <Title {...restProps}>{children}</Title>;
};

ExtendedWorks.Image = function ExtendedWorksImage({ children, ...restProps }) {
  return <Image {...restProps}>{children}</Image>;
};

ExtendedWorks.RenderImage = function ExtendedWorksRenderImage({
  children,
  ...restProps
}) {
  return <RenderImage {...restProps}>{children}</RenderImage>;
};

ExtendedWorks.Content = function ExtendedWorksContent({
  children,
  ...restProps
}) {
  return <Content {...restProps}>{children}</Content>;
};

ExtendedWorks.Link = function ExtendedWorksLink({ children, ...restProps }) {
  return <Link {...restProps}>{children}</Link>;
};
