import React, { useState } from "react";
import { Header, Navbar, Slidebar } from "../../components";
import { about } from "../../data/about";

export default function HeaderContainer() {
  const [showSlidebar, changeSlidebar] = useState(false);

  var sliderFunction = () => {
    changeSlidebar(!showSlidebar);
  };

  return (
    <Header showImage={showSlidebar ? false : true}>
      {showSlidebar ? (
        <Slidebar>
          <Slidebar.CloseButton onClick={sliderFunction} />
          <Slidebar.Group>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL}
              onClick={sliderFunction}
            >
              Home
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/aboutme"}
              onClick={sliderFunction}
            >
              About Me
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/myworks"}
              onClick={sliderFunction}
            >
              My Works
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/contact"}
              onClick={sliderFunction}
            >
              Contact
            </Slidebar.LinkText>
          </Slidebar.Group>
        </Slidebar>
      ) : null}
      {showSlidebar ? null : (
        <>
          <Navbar>
            <Navbar.Group>
              <Navbar.List>
                <Navbar.ListLi>
                  <Navbar.ListA active to={process.env.PUBLIC_URL}>
                    Home
                  </Navbar.ListA>
                </Navbar.ListLi>
                {/* <Navbar.Divider /> */}
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL + "/aboutme"}>
                    About Me
                  </Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL + "/myworks"}>
                    My Works
                  </Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL + "/contact"}>
                    Contact
                  </Navbar.ListA>
                </Navbar.ListLi>
              </Navbar.List>
            </Navbar.Group>
              <Navbar.Mobile onClick={sliderFunction} />
            <Navbar.Group1>
              <Navbar.Name>
                <Navbar.Initial>{about.initial}</Navbar.Initial> {about.name}
              </Navbar.Name>
            </Navbar.Group1>
          </Navbar>
          <Header.Group>
            <Header.Name>
              <Header.NameBold>ARCHI</Header.NameBold>TECTURE PORTFOLIO
            </Header.Name>
            {/* <br /> */}
            <Header.OwnerName>
              SABARISH <Navbar.Initial size="4rem">JB</Navbar.Initial>
            </Header.OwnerName>
          </Header.Group>
          <Header.Group1>
            <Header.OwnerName>
              Hi, I'am{" "}
              <Navbar.Initial size="4rem">{about.initial}...</Navbar.Initial>
            </Header.OwnerName>
            <Header.Name size={"5rem"} padding={"10px"} weight={"bold"}>
              {about.heading}
            </Header.Name>
            <Header.OwnerName size={"2rem"} padding={"10px"}>
              {about.content}
            </Header.OwnerName>
            <Header.Button to={process.env.PUBLIC_URL + "/aboutme"}>
              About Me
            </Header.Button>
          </Header.Group1>{" "}
        </>
      )}
    </Header>
  );
}
