import styled from "styled-components";
import { backgroundColorY } from "../../themes/color";
import { Link as ReactRouterLink } from "react-router-dom";

export const Container = styled.div`
  margin-left: 50px;
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
`;

export const RenderContainer = styled.div`
  margin-left: 50px;
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
`;

export const Group = styled.div`
  ${"" /* flex: 1; */}
  flex: 0 35%;
  display: border-box;
  border: 5px solid white;
  margin: 50px;
  border-radius: 50px;
  transition: 0.5s all ease;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  ${'' /* height: 300px; */}

  @media (max-width: 650px) {
    flex: 0 50%;
  }

  &:hover {
    flex: 2;
    transition: 0.5s all ease;
  }
`;

export const RenderGroup = styled.div`
  flex: 0 30%;
  display: flex;
  transition: 0.5s all ease;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;

  @media (max-width: 650px) {
    flex: 0 45%;
  }
`;

export const Title = styled.h1`
  text-align: ${({ left }) => (left ? "left" : "center")};
  margin-top: 15px;
  margin-bottom: ${({ margin }) => (margin ? "0px" : "15px")};
  margin-left: ${({ margin }) => (margin ? "100px" : "0")};
  color: ${backgroundColorY};
  font-size: ${({ fontBig }) => (fontBig ? "4.5rem" : "auto")};

  @media (max-width: 650px) {
    font-size: 1rem;
  }
`;

export const Image = styled.img`
  width: 100%;
  margin-bottom: 15px;
`;

export const RenderImage = styled.img`
  width: 100%;
  align-items: center;
  margin-bottom: 15px;
`;

export const Content = styled.img``;

export const Link = styled(ReactRouterLink)`
  text-decoration: none;
`;
