import React from "react";
import {
  Container,
  Title,
  Image,
  Content,
  Group,
  Video,
  VideoGroup,
  CornerImage,
  QrImage,
  OverlayText,
} from "./extendedworkcontent";

export default function ExtendedWorksContent({ children, ...restProps }) {
  console.log(restProps);
  return <Container {...restProps}>{children}</Container>;
}

ExtendedWorksContent.Group = function ExtendedWorksContentGroup({
  children,
  ...restProps
}) {
  return <Group {...restProps}>{children}</Group>;
};

ExtendedWorksContent.VideoGroup = function ExtendedWorksContentVideoGroup({
  children,
  ...restProps
}) {
  return <VideoGroup {...restProps}>{children}</VideoGroup>;
};

ExtendedWorksContent.Title = function ExtendedWorksContentTitle({
  children,
  ...restProps
}) {
  return <Title {...restProps}>{children}</Title>;
};

ExtendedWorksContent.OverlayText = function ExtendedWorksContentOverlayText({
  children,
  ...restProps
}) {
  return <OverlayText {...restProps}>{children}</OverlayText>;
};

ExtendedWorksContent.Image = function ExtendedWorksContentImage({
  children,
  ...restProps
}) {
  return <Image {...restProps} />;
};

ExtendedWorksContent.CornerImage = function ExtendedWorksContentCornerImage({
  children,
  ...restProps
}) {
  return <CornerImage {...restProps} />;
};

ExtendedWorksContent.QrImage = function ExtendedWorksContentQrImage({
  children,
  ...restProps
}) {
  return <QrImage {...restProps} />;
};

ExtendedWorksContent.Video = function ExtendedWorksContentVideo({
  children,
  ...restProps
}) {
  return <Video {...restProps}>{children}</Video>;
};

ExtendedWorksContent.Content = function ExtendedWorksContentContent({
  children,
  ...restProps
}) {
  return <Content {...restProps}>{children}</Content>;
};
