import React from "react";
import { Container, Group, CloseButton, Button, LinkText } from "./slidebar";

export default function Slidebar({ children, ...restProps }) {
  return <Container {...restProps}>{children}</Container>;
}

Slidebar.Group = function SlidebarGroup({ children, ...restProps }) {
  return <Group {...restProps}>{children}</Group>;
};

Slidebar.Button = function SlidebarButton({ children, ...restProps }) {
  return <Button {...restProps}>{children}</Button>;
};

Slidebar.LinkText = function SlidebarLinkText({ children, ...restProps }) {
  return <LinkText {...restProps}>{children}</LinkText>;
};

Slidebar.CloseButton = function SlidebarCloseButton({
  children,
  ...restProps
}) {
  return <CloseButton {...restProps}>{children}</CloseButton>;
};
