import styled from "styled-components/macro";
import { Link as ReactRouterLink } from "react-router-dom";
import { backgroundColorB, backgroundColorY } from "../../themes/color";

export const Container = styled.div`
  background-color: ${backgroundColorB};
  display: flex;
  flex-direction: row;
  color: #fff;
  flex-wrap: wrap;
  flex: 0% 50%;
  align-items: center;
  justify-content: center;

  @media (max-width: 1300px) {
    flex: 0% 40%;
  }
`;

export const Image = styled.img`
  width: 100%;
  height: 300px;
  margin-bottom: 15px;
  transition: 0.5s all ease;
  display: none;

  @media (max-width: 650px) {
    height: 200px;
    width: 100%;
  }
`;

export const Group = styled.div`
  ${"" /* flex: 1; */}
  height: 450px;
  width: 40%;
  border: 5px solid white;
  margin: 50px;
  border-radius: 50px;
  transition: 0.5s all ease;
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  justify-content: center;

  @media (max-width: 650px) {
    height: 200px;
    width: 60%;
    align-items: center;
    justify-content: center;
  }

  &:hover {
    transition: 0.5s all ease;
    cursor: pointer;
    height: auto;
    justify-content: start;
    align-items: start;

    ${Image} {
      display: block;
    }
  }
`;

export const Title = styled.h1`
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
  color: ${backgroundColorY};
  align-items: center;
  font-size: 4rem;

  @media (max-width: 650px) {
    font-size: 2rem;
  }
`;

export const Link = styled(ReactRouterLink)`
  text-decoration: none;
`;
