import styled from "styled-components";
import { backgroundColorY } from "../../themes/color";

export const Container = styled.div`
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 50px;
  display: ${({ onlyImage }) => (onlyImage ? "none" : "flex")};
  flex-direction: column;
`;

export const Group = styled.div`
  margin-top: 80px;
  align-items: center;
  padding-right: 20px;
`;

export const Title = styled.h1`
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
  color: ${backgroundColorY};
`;

export const Image = styled.img`
  width: 85%;
  margin: auto;
  margin-bottom: 15px;
`;

export const CornerImage = styled.img`
  width: 15%;
  height: 15%;
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0.7;
`;

export const QrImage = styled.img`
  width: 7%;
  height: 11%;
  position: absolute;
  bottom: 60px;
  right: 30px;
`;

export const OverlayText = styled.p`
  position: absolute;
  width: auto;
  bottom: 30px;
  right: 5px;
  color: white;
`;

export const Video = styled.video`
  width: 100%;
  height: 85%;
  position: relative;
  margin-bottom: 15px;
`;

export const Content = styled.p`
  font-size: 1.5rem;
`;

export const VideoGroup = styled.div`
  position: relative;
`;
