import React, { useState } from "react";
import { Header, Navbar, Aboutme, Slidebar } from "../../components";
import {
  about,
  languages,
  skills,
  objectives,
  education,
  personalStrength,
  dob,
  address,
  educationBio,
  phone,
  mail,
} from "../../data/about";

export default function AboutmeContainer() {
  var showPoints = (length) => {
    var numberOfPoints = [];
    for (var i = 0; i < length; i++) {
      numberOfPoints.push(<Aboutme.RoundContainerColor key={i} />);
    }
    return numberOfPoints;
  };

  var showNonPoints = (length) => {
    var numberOfNonPoints = [];
    for (var i = 0; i < length; i++) {
      numberOfNonPoints.push(
        <Aboutme.RoundContainerColor borderalone key={i} />
      );
    }
    return numberOfNonPoints;
  };

  const [showSlidebar, changeSlidebar] = useState(false);

  var sliderFunction = () => {
    changeSlidebar(!showSlidebar);
  };

  return (
    <Header showImage={false} height>
      {showSlidebar ? (
        <Slidebar>
          <Slidebar.CloseButton onClick={sliderFunction} />
          <Slidebar.Group>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL}
              onClick={sliderFunction}
            >
              Home
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/aboutme"}
              onClick={sliderFunction}
            >
              About Me
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/myworks"}
              onClick={sliderFunction}
            >
              My Works
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/contact"}
              onClick={sliderFunction}
            >
              Contact
            </Slidebar.LinkText>
          </Slidebar.Group>
        </Slidebar>
      ) : null}
      {showSlidebar ? null : (
        <>
          <Navbar>
            <Navbar.Group>
              <Navbar.List>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL}>Home</Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA active to={process.env.PUBLIC_URL + "/aboutme"}>
                    About Me
                  </Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL + "/myworks"}>
                    My Works
                  </Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL + "/contact"}>
                    Contact
                  </Navbar.ListA>
                </Navbar.ListLi>
              </Navbar.List>
            </Navbar.Group>
            <Navbar.Mobile onClick={sliderFunction} />
            <Navbar.Group1>
              <Navbar.Name>
                <Navbar.Initial>{about.initial}</Navbar.Initial> {about.name}
              </Navbar.Name>
            </Navbar.Group1>
          </Navbar>
          <Navbar.HoverGroup>
            <Navbar.List>
              <Navbar.ListLi2>
                <Navbar.ListA to="/">Office Wors</Navbar.ListA>
              </Navbar.ListLi2>
              <Navbar.ListLi2>
                <Navbar.ListA to="/">Individual Wors</Navbar.ListA>
              </Navbar.ListLi2>
              <Navbar.ListLi2>
                <Navbar.ListA to="/">College Wors</Navbar.ListA>
              </Navbar.ListLi2>
            </Navbar.List>
          </Navbar.HoverGroup>
          <Aboutme>
            <Aboutme.Group>
              <Aboutme.Image
                src={process.env.PUBLIC_URL + "/images/about_Profile.png"}
              />
              <Aboutme.GroupInfo>
                <Aboutme.InfoImage
                  src={process.env.PUBLIC_URL + "/images/about/dob.png"}
                />
                <Aboutme.InfoText>{dob}</Aboutme.InfoText>
              </Aboutme.GroupInfo>
              <Aboutme.GroupInfo>
                <Aboutme.InfoImage
                  src={process.env.PUBLIC_URL + "/images/about/address.png"}
                />
                <Aboutme.InfoText>
                  {address.map((item) => {
                    return [item, <br />];
                  })}
                </Aboutme.InfoText>
              </Aboutme.GroupInfo>
              <Aboutme.GroupInfo>
                <Aboutme.InfoImage
                  src={process.env.PUBLIC_URL + "/images/about/college.png"}
                />
                <Aboutme.InfoText>{educationBio}</Aboutme.InfoText>
              </Aboutme.GroupInfo>
              <Aboutme.GroupInfo>
                <Aboutme.InfoImage
                  src={process.env.PUBLIC_URL + "/images/about/call.png"}
                />
                <Aboutme.InfoText>{phone}</Aboutme.InfoText>
              </Aboutme.GroupInfo>
              <Aboutme.GroupInfo>
                <Aboutme.InfoImage
                  src={process.env.PUBLIC_URL + "/images/about/mail.png"}
                />
                <Aboutme.InfoText>
                  {mail.map((item) => {
                    return [item, <br />];
                  })}
                </Aboutme.InfoText>
              </Aboutme.GroupInfo>
            </Aboutme.Group>
            <Aboutme.Group2>
              <Aboutme.Group3>
                <Aboutme.Image
                  src={process.env.PUBLIC_URL + "/images/about_Profile.png"}
                />
                <Aboutme.GroupInfo>
                  <Aboutme.InfoImage
                    src={process.env.PUBLIC_URL + "/images/about/dob.png"}
                  />
                  <Aboutme.InfoText>{dob}</Aboutme.InfoText>
                </Aboutme.GroupInfo>
                <Aboutme.GroupInfo>
                  <Aboutme.InfoImage
                    src={process.env.PUBLIC_URL + "/images/about/address.png"}
                  />
                  <Aboutme.InfoText>
                    {address.map((item) => {
                      return [item, <br />];
                    })}
                  </Aboutme.InfoText>
                </Aboutme.GroupInfo>
                <Aboutme.GroupInfo>
                  <Aboutme.InfoImage
                    src={process.env.PUBLIC_URL + "/images/about/college.png"}
                  />
                  <Aboutme.InfoText>{educationBio}</Aboutme.InfoText>
                </Aboutme.GroupInfo>
                <Aboutme.GroupInfo>
                  <Aboutme.InfoImage
                    src={process.env.PUBLIC_URL + "/images/about/call.png"}
                  />
                  <Aboutme.InfoText>{phone}</Aboutme.InfoText>
                </Aboutme.GroupInfo>
                <Aboutme.GroupInfo>
                  <Aboutme.InfoImage
                    src={process.env.PUBLIC_URL + "/images/about/mail.png"}
                  />
                  <Aboutme.InfoText>
                    {mail.map((item) => {
                      return [item, <br />];
                    })}
                  </Aboutme.InfoText>
                </Aboutme.GroupInfo>
              </Aboutme.Group3>
              <Aboutme.Title>OBJECTIVES</Aboutme.Title>
              <Aboutme.Content>{objectives}</Aboutme.Content>
              <Aboutme.Title>EDUCATION</Aboutme.Title>
              <Aboutme.Content>
                {education.map((item) => {
                  return [item, <br />];
                })}
              </Aboutme.Content>
              <Aboutme.Title>TECHNICAL SKILLS</Aboutme.Title>
              <Aboutme.Content>
                {skills.map((item, index) => {
                  return (
                    <Aboutme.GroupRow key={index}>
                      <Aboutme.SpanwidthSkills>
                        {item[0]}
                      </Aboutme.SpanwidthSkills>
                      {showPoints(item[1])}
                      {showNonPoints(item[2])}
                    </Aboutme.GroupRow>
                  );
                })}
              </Aboutme.Content>
              <Aboutme.Title>KNOWN LANGUAGES</Aboutme.Title>
              <Aboutme.Content>
                {languages.map((item, index) => {
                  return (
                    <Aboutme.GroupRow key={index}>
                      <Aboutme.Spanwidth>{item[0]}</Aboutme.Spanwidth>
                      {showPoints(item[1])}
                      {showNonPoints(item[2])}
                    </Aboutme.GroupRow>
                  );
                })}
              </Aboutme.Content>
              <Aboutme.Title>PERSONAL STRENGTH</Aboutme.Title>
              <Aboutme.Content>
                {personalStrength.map((item) => {
                  return ["- " + item, <br />];
                })}
              </Aboutme.Content>
              <Aboutme.Title>WORK EXPERIENCE</Aboutme.Title>
              <Aboutme.Content>
                1.INTERN EXPERIENCE
                <Aboutme.ExperienceP1>
                  - ARTFORMS ARCHITECTURE, BANGLORE, KARNATAKA.
                </Aboutme.ExperienceP1>
                <Aboutme.ExperienceImg1
                  src={process.env.PUBLIC_URL + "/images/about/artform.png"}
                />
                <Aboutme.ExperienceP2>
                  - A DESIGN STUDIO, CHENNAI, TAMIL NADU.
                </Aboutme.ExperienceP2>
                <Aboutme.ExperienceImg2
                  src={process.env.PUBLIC_URL + "/images/about/design.png"}
                />
                2.JOB EXPERIENCE
                <Aboutme.ExperienceP3>
                  - NIDHI INTERIORS AND ARCHITECHS, BANGLORE, KARNATAKA.
                </Aboutme.ExperienceP3>
                <Aboutme.ExperienceImg3
                  src={process.env.PUBLIC_URL + "/images/about/nidhi.png"}
                />
                <Aboutme.ExperienceP4>
                  - KM CONSTRUCTIONS, THENI, TAMIL NADU
                </Aboutme.ExperienceP4>
                <Aboutme.ExperienceImg4
                  src={process.env.PUBLIC_URL + "/images/about/kmc.png"}
                />
              </Aboutme.Content>
            </Aboutme.Group2>
          </Aboutme>
        </>
      )}
    </Header>
  );
}
