import styled from "styled-components";
import { backgroundColorY } from "../../themes/color";

export const Container = styled.div`
  margin-left: 50px;
  margin-top: 50px;
  ${"" /* width: 50%; */}
  display: flex;

  @media (max-width: 1200px) {
    margin-left: 10px;
    margin-top: 10px;
  }
`;

export const Group = styled.div`
  width: 30%;
  height: 100%;

  @media (max-width: 900px) {
    display: none;
  }
`;

export const Group2 = styled.div`
  width: 67.5%;
  height: 100%;
  margin-left: 2.5%;

  @media (max-width: 900px) {
    width: 100%;
    margin-right: 25px;
  }
`;

export const Group3 = styled.div`
  display: none;

  @media (max-width: 900px) {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-right: 25px;
  }
`;

export const GroupRow = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
`;

export const Image = styled.img`
  width: 100%;

  @media (max-width: 900px) {
    width: 50%;
    margin: auto;
  }
`;

export const Title = styled.p`
  font-size: 1.75rem;
  margin-bottom: 10px;
  color: ${backgroundColorY};
`;

export const Content = styled.p`
  font-size: 1.2rem;
  margin-bottom: 10px;
  margin-right: 15px;

  @media (max-width: 1200px) {
    font-size: 18px;
  }
  @media (max-width: 960px) {
    font-size: 16px;
  }
`;

export const InfoGroup = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;

  @media (max-width: 1200px) {
    margin-bottom: 20px;
    width: 50%;
    margin: auto;
  }
  @media (max-width: 960px) {
    margin-bottom: 20px;
    width: 50%;
    margin: auto;
  }
`;

export const InfoText = styled.p`
  margin-right: 10px;
  font-size: 1.25rem;

  @media (max-width: 1200px) {
    margin-bottom: 20px;
  }
  @media (max-width: 960px) {
    margin-bottom: 20px;
  }
  @media (max-width: 650px) {
    font-size: 1.1rem;
  }
`;

export const InfoImage = styled.img`
  width: 125px;
  height: 125px;
  @media (max-width: 1200px) {
    width: 75px;
    height: 75px;
  }
`;

export const ExperienceImg1 = styled.img`
  display: none;
  width: 500px;
  height: 125px;
  margin-top: 10px;
  margin-bottom: 10px;
  transition: 0.5s all ease;

  @media (max-width: 1200px) {
    width: 300px;
    height: 100px;
  }
  @media (max-width: 960px) {
    width: 200px;
    height: 100px;
  }
`;

export const ExperienceImg2 = styled.img`
  display: none;
  width: 500px;
  height: 125px;
  margin-top: 10px;
  transition: 0.5s all ease;
  margin-bottom: 10px;

  @media (max-width: 1200px) {
    width: 300px;
    height: 100px;
  }
  @media (max-width: 960px) {
    width: 200px;
    height: 100px;
  }
`;

export const ExperienceImg3 = styled.img`
  display: none;
  width: 500px;
  height: 125px;
  margin-top: 10px;
  transition: 0.5s all ease;
  margin-bottom: 10px;

  @media (max-width: 1200px) {
    width: 300px;
    height: 100px;
  }
  @media (max-width: 960px) {
    width: 200px;
    height: 100px;
  }
`;

export const ExperienceImg4 = styled.img`
  width: 500px;
  height: 125px;
  margin-top: 10px;
  margin-bottom: 10px;
  transition: 0.5s all ease;
  display: none;

  @media (max-width: 1200px) {
    width: 300px;
    height: 100px;
  }
  @media (max-width: 960px) {
    width: 200px;
    height: 100px;
  }
`;

export const ExperienceP1 = styled.p`
  margin: 0;
  padding: 0;
  padding: 10px;

  &:hover + ${ExperienceImg1} {
    display: block;
  }
`;

export const ExperienceP2 = styled.p`
  margin: 0;
  padding: 0;
  padding: 10px;

  &:hover + ${ExperienceImg2} {
    display: block;
  }
`;

export const ExperienceP3 = styled.p`
  margin: 0;
  padding: 0;
  padding: 10px;

  &:hover + ${ExperienceImg3} {
    display: block;
  }
`;

export const ExperienceP4 = styled.p`
  margin: 0;
  padding: 0;
  padding: 10px;

  &:hover + ${ExperienceImg4} {
    display: block;
  }
`;

export const RoundContainer = styled.div`
  height: 15px;
  width: 15px;
  background-color: ${({ borderalone }) =>
    borderalone ? "" : backgroundColorY};
  border-color: coral;
  border-radius: 10px;
  border: 2px solid ${backgroundColorY};
  display: inline-block;
  margin-right: 8px;
  margin-bottom: 8px;
`;

export const Spanwidth = styled.p`
  width: 125px;

  @media (max-width: 1200px) {
    width: 100px;
  }
  @media (max-width: 960px) {
    width: 75px;
  }
`;

export const SpanwidthSkills = styled.p`
  width: 225px;

  @media (max-width: 1200px) {
    width: 200px;
  }
  @media (max-width: 960px) {
    width: 165px;
  }
`;
