import React from "react";
import { Header, ExtendedWorks, Navbar } from "../../components";
import { about } from "../../data/about";

export default function ExtendedContainer({ children, ...restProps }) {
  return (
    <Header showImage={false}>
      <Navbar>
        <Navbar.Group>
          <Navbar.List>
            <Navbar.ListLi>
              <Navbar.ListA to={process.env.PUBLIC_URL}>Home</Navbar.ListA>
            </Navbar.ListLi>
            <Navbar.ListLi>
              <Navbar.ListA to={process.env.PUBLIC_URL +"/aboutme"}>About Me</Navbar.ListA>
            </Navbar.ListLi>
            <Navbar.ListLi>
              <Navbar.ListA active to={process.env.PUBLIC_URL +"/myworks"}>
                My Works
              </Navbar.ListA>
            </Navbar.ListLi>
            <Navbar.ListLi>
              <Navbar.ListA to={process.env.PUBLIC_URL +"/contact"}>Contact</Navbar.ListA>
            </Navbar.ListLi>
          </Navbar.List>
        </Navbar.Group>
        <Navbar.Group>
          <Navbar.Name>
            <Navbar.Initial>{about.initial}</Navbar.Initial> {about.name}
          </Navbar.Name>
        </Navbar.Group>
      </Navbar>
      <ExtendedWorks.Title left fontBig bold margin>
        COLLEGE WORK
      </ExtendedWorks.Title>
      <ExtendedWorks>
        <ExtendedWorks.Group>
          <ExtendedWorks.Link to={process.env.PUBLIC_URL +"/extendedworkcontent"}>
            <ExtendedWorks.Title>Work 1</ExtendedWorks.Title>
            <ExtendedWorks.Image src={process.env.PUBLIC_URL + "/images/college 1.jpeg"} />
          </ExtendedWorks.Link>
        </ExtendedWorks.Group>
        <ExtendedWorks.Group>
          <ExtendedWorks.Link to={process.env.PUBLIC_URL +"/extendedworkcontent"}>
            <ExtendedWorks.Title>Work 2</ExtendedWorks.Title>
            <ExtendedWorks.Image src={process.env.PUBLIC_URL + "/images/college 1.jpeg"} />
          </ExtendedWorks.Link>
        </ExtendedWorks.Group>
      </ExtendedWorks>
    </Header>
  );
}
