import React, { useState } from "react";
import { Header, Navbar, Contact, Slidebar } from "../../components";
import { about } from "../../data/about";
import * as emailjs from "emailjs-com";
import { init } from "emailjs-com";

init("user_2v833bJLcIexSHS1fizST");

export default function ContactContainer() {
  const [showSlidebar, changeSlidebar] = useState(false);
  const [email, changeEmail] = useState("");
  const [message, changeMessage] = useState("");
  const [name, changeName] = useState("");

  var sliderFunction = () => {
    changeSlidebar(!showSlidebar);
  };

  function handleSubmit(e) {
    e.preventDefault();
    let templateParams = {
      from_name: email,
      to_name: "sabarchi07@gmail.com",
      subject: "Job",
      message: message,
    };
    emailjs.send(
      "service_cw34wym",
      "template_ruhq8ic",
      templateParams,
      "user_2v833bJLcIexSHS1fizST"
    );
    resetForm();
  }

  function resetForm() {
    changeEmail("");
    changeMessage("");
    changeName("");
  }

  return (
    <Header showImage={showSlidebar ? false : true}>
      {showSlidebar ? (
        <Slidebar>
          <Slidebar.CloseButton onClick={sliderFunction} />
          <Slidebar.Group>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL}
              onClick={sliderFunction}
            >
              Home
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/aboutme"}
              onClick={sliderFunction}
            >
              About Me
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/myworks"}
              onClick={sliderFunction}
            >
              My Works
            </Slidebar.LinkText>
            <Slidebar.LinkText
              to={process.env.PUBLIC_URL + "/contact"}
              onClick={sliderFunction}
            >
              Contact
            </Slidebar.LinkText>
          </Slidebar.Group>
        </Slidebar>
      ) : null}
      {showSlidebar ? null : (
        <>
          <Navbar>
            <Navbar.Group>
              <Navbar.List>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL}>Home</Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL + "/aboutme"}>
                    About Me
                  </Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA to={process.env.PUBLIC_URL + "/myworks"}>
                    My Works
                  </Navbar.ListA>
                </Navbar.ListLi>
                <Navbar.ListLi>
                  <Navbar.ListA active to={process.env.PUBLIC_URL + "/contact"}>
                    Contact
                  </Navbar.ListA>
                </Navbar.ListLi>
              </Navbar.List>
            </Navbar.Group>
            <Navbar.Mobile onClick={sliderFunction} />
            <Navbar.Group1>
              <Navbar.Name>
                <Navbar.Initial>{about.initial}</Navbar.Initial> {about.name}
              </Navbar.Name>
            </Navbar.Group1>
          </Navbar>
          <Contact>
            <Contact.Group>
              <Contact.MaintitleLets>Let's</Contact.MaintitleLets>
              <Contact.Maintitle> talk.</Contact.Maintitle>
            </Contact.Group>
            <Contact.Title>E-Mail</Contact.Title>
            <Contact.TextField
              value={email}
              type="text"
              placeholder="Enter your E-Mail ID"
              onChange={(e) => changeEmail(e.target.value)}
            />
            <Contact.Title>Name</Contact.Title>
            <Contact.TextField
              value={name}
              type="text"
              placeholder="Enter your Name"
              onChange={(e) => changeName(e.target.value)}
            />
            <Contact.Title>Message</Contact.Title>
            <Contact.Textarea
              value={message}
              type="text"
              placeholder="Enter your Message"
              onChange={(e) => changeMessage(e.target.value)}
            />
            <Contact.Button onClick={handleSubmit}>Submit</Contact.Button>
          </Contact>
        </>
      )}
    </Header>
  );
}
