import React from "react";
import {
  Container,
  Group,
  List,
  ListLi,
  ListA,
  Name,
  Initial,
  HoverGroup,
  ListLi2,
  Group1,
  Mobile,
} from "./navbar";

export default function Navbar({ children, ...restProps }) {
  return <Container {...restProps}>{children}</Container>;
}

Navbar.Group = function NavbarGroup({ children, ...restProps }) {
  return <Group {...restProps}>{children}</Group>;
};

Navbar.Group1 = function NavbarGroup1({ children, ...restProps }) {
  return <Group1 {...restProps}>{children}</Group1>;
};

Navbar.Mobile = function NavbarMobile({ children, ...restProps }) {
  return <Mobile {...restProps}>{children}</Mobile>;
};

Navbar.HoverGroup = function NavbarHoverGroup({ children, ...restProps }) {
  return <HoverGroup {...restProps}>{children}</HoverGroup>;
};

Navbar.List = function NavbarList({ children, ...restProps }) {
  return <List {...restProps}>{children}</List>;
};

Navbar.ListLi = function NavbarListLi({ children, ...restProps }) {
  return <ListLi {...restProps}>{children}</ListLi>;
};

Navbar.ListLi2 = function NavbarListLi2({ children, ...restProps }) {
  return <ListLi2 {...restProps}>{children}</ListLi2>;
};

Navbar.ListA = function NavbarListA({ children, ...restProps }) {
  return <ListA {...restProps}>{children}</ListA>;
};

Navbar.Initial = function NavbarInitial({ children, ...restProps }) {
  return <Initial {...restProps}>{children}</Initial>;
};

Navbar.Name = function NavbarName({ children, ...restProps }) {
  return <Name {...restProps}>{children}</Name>;
};
