import { Link as ReactRouterLink } from "react-router-dom";
import styled, { keyframes } from "styled-components/macro";
import {
  backgroundColorB,
  backgroundColorY,
  backgroundColorW,
} from "../../themes/color";
import { about } from "../../data/about";

export const Container = styled.div`
  background-color: ${backgroundColorB};
  background-image : url(${about.image});
  background-image : ${({showImage}) => showImage ? '' : 'none'};
  background-position: 50% 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: ${({height}) => height ? 'auto' : '100vh'};
  width: 100%;
  color: #fff;
`;

export const Group = styled.div`
  display: ${({ hide }) => (hide ? "none" : "flex")};
  flex-direction: column;
  justify-content: center;
  width: 50%;
  height: 93vh;
  margin-left: 60px;
  margin-right: 60px;
`;

export const Group1 = styled.div`
  display: ${({ hide }) => (hide ? "flex" : "none")};
  flex-direction: column;
  justify-content: center;
  width: 50%;
  height: 93vh;
  margin-left: 60px;
  margin-right: 60px;
`;

const slideIn = keyframes`
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
`;

export const Name = styled.p`
  font-size: ${({ size }) => (size ? size : "8rem")};
  padding-top: ${({ padding }) => (padding ? padding : "0")};
  font-weight: ${({ weight }) => (weight ? weight : "normal")};
  animation: ${slideIn} 2s cubic-bezier(0.4, 0, 0.2, 1) both;

  @media (max-width: 1000px) {
    font-size: 6rem;
  }
  @media (max-width: 740px) {
    font-size: 4rem;
  }
  @media (max-width: 555px) {
    font-size: 2rem;
  }
`;

export const NameBold = styled.p`
  font-size: 9rem;
  font-weight: bold;
  display: inline;
  color: ${backgroundColorY};

  @media (max-width: 1000px) {
    font-size: 7rem;
  }
  @media (max-width: 740px) {
    font-size: 5rem;
  }
  @media (max-width: 555px) {
    font-size: 3rem;
  }
`;

export const OwnerName = styled.p`
  font-size: ${({ size }) => (size ? size : "3rem")};
  display: inline;
  padding-top: ${({ padding }) => (padding ? padding : "0")};
  color: ${backgroundColorW};
  letter-spacing: 1.5px;
  animation: ${slideIn} 2s cubic-bezier(0.4, 0, 0.2, 1) both;
`;

export const Button = styled(ReactRouterLink)`
  margin-top: 30px;
  text-decoration: none;
  color: inherit;
  font-size: 1.5rem;
  border: 2px solid;
  width: 100px;
  padding: 10px;
  color: ${backgroundColorY};
  animation: ${slideIn} 2s cubic-bezier(0.4, 0, 0.2, 1) both;
  border-radius: 15px;

  &:hover {
    color: ${backgroundColorW};
    transition: 0.8s all ease;
  }
`;
