import React from "react";
import { Header, ExtendedWorks, Navbar } from "../../components";
import { about } from "../../data/about";
import { render } from "../../data/works";

export default function extendedworkrendercontent({ children, ...restProps }) {
  return (
    <Header showImage={false} height>
      <Navbar>
        <Navbar.Group>
          <Navbar.List>
            <Navbar.ListLi>
              <Navbar.ListA to={process.env.PUBLIC_URL}>Home</Navbar.ListA>
            </Navbar.ListLi>
            <Navbar.ListLi>
              <Navbar.ListA to={process.env.PUBLIC_URL + "/aboutme"}>
                About Me
              </Navbar.ListA>
            </Navbar.ListLi>
            <Navbar.ListLi>
              <Navbar.ListA active to={process.env.PUBLIC_URL + "/myworks"}>
                My Works
              </Navbar.ListA>
            </Navbar.ListLi>
            <Navbar.ListLi>
              <Navbar.ListA to={process.env.PUBLIC_URL + "/contact"}>
                Contact
              </Navbar.ListA>
            </Navbar.ListLi>
          </Navbar.List>
        </Navbar.Group>
        <Navbar.Group>
          <Navbar.Name>
            <Navbar.Initial>{about.initial}</Navbar.Initial> {about.name}
          </Navbar.Name>
        </Navbar.Group>
      </Navbar>
      <ExtendedWorks.Title left fontBig bold margin>
        RENDER WORK
      </ExtendedWorks.Title>
      <ExtendedWorks.RenderContainer>
        {render.map((item) => {
          return item.images.map((item1) => {
            return (
              <ExtendedWorks.RenderGroup onClick={() => console.log("click")}>
                <ExtendedWorks.RenderImage
                  src={
                    process.env.PUBLIC_URL +
                    "/images/works/" +
                    item.imageFolderName +
                    "/" +
                    item1
                  }
                />
              </ExtendedWorks.RenderGroup>
            );
          });
        })}
      </ExtendedWorks.RenderContainer>
    </Header>
  );
}
