import styled, { keyframes } from "styled-components/macro";
import {
  backgroundColorB,
  backgroundColorY,
  backgroundColorW,
} from "../../themes/color";

export const Container = styled.div`
  padding: 50px;
  align-items: center;
`;

export const Title = styled.p`
  font-size: 1.5rem;
  color: ${backgroundColorY};
`;

export const Maintitle = styled.p`
  font-size: 3rem;
  font-weight: normal;
  color: ${backgroundColorW};
  margin-bottom: 10px;
  margin-left: 16px;
`;

export const MaintitleLets = styled.p`
  font-size: 3.7rem;
  font-weight: bold;
  color: ${backgroundColorW};
  margin-bottom: 20px;
`;

export const TextField = styled.input`
  width: 40%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 0.8px solid white;
  border-radius: 2px;
  background-color: ${backgroundColorB};
  color: white;
  font-size: 1.2rem;
`;

export const Textarea = styled.textarea`
  width: 40%;
  max-width: 40%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 0.8px solid white;
  border-radius: 2px;
  background-color: ${backgroundColorB};
  color: white;
  font-size: 1.2rem;
`;

const slideIn = keyframes`
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
`;

export const Button = styled.div`
  margin-top: 30px;
  text-decoration: none;
  color: inherit;
  font-size: 1.5rem;
  border: 2px solid;
  width: 85px;
  padding: 10px;
  color: ${backgroundColorY};
  animation: ${slideIn} 2s cubic-bezier(0.4, 0, 0.2, 1) both;
  border-radius: 15px;

  &:hover {
    color: ${backgroundColorW};
    transition: 0.8s all ease;
    cursor: pointer;
  }
`;

export const Group = styled.div`
  height: auto;
  display: flex;
  align-items: center;
`;